@import 'partials/colours';
@import 'partials/normalize';

@import 'partials/buttons';
@import 'partials/footer';
@import 'partials/inputs';
@import 'partials/layout';
@import 'partials/navigation';
@import 'partials/quotes';
@import 'partials/tables';
@import 'partials/text';

*,
*:after,
*:before {
    box-sizing: inherit;
}

html {
    font-size: 62.5%;
    height: 100%;
}

body {
    font-family: 'Roboto', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
    font-size: 1.6em;
    font-weight: 300;
    letter-spacing: .01em;
    line-height: 1.6;

    height: 100%;

    position: relative;

    color: $text-color;
	background: $background-color;
}

container {
	min-height: 100%;
	display: grid;
	grid-template-rows: auto 1fr auto;
	grid-template-columns: 100%;
}

main {
    margin: 0 auto;
    max-width: 112.0rem;
	padding: 0rem 0rem;
	padding-top: 2.0rem;
    position: relative;
    width: 100%;

	min-height: 100%;
	display: grid;
	grid-template-rows: auto 1fr auto;
	grid-template-columns: 100%;
}